import { LocalStorageService } from './localStorage.service';
import songsList from "../data/songs-list";
export class numberInputService {
    public number: number;
    public ready_to_clear: boolean;
    public reverse: boolean;

    constructor() {
        let lss = LocalStorageService.getLocalStorage()
        if (lss) {
            this.number = lss.last
        }else{
            this.number = 1
        }
    }

    concatenate(n: number) {
        if (this.number) {
            let condition

            if (this.reverse) condition = n + "" + this.number;

            else condition = this.number + "" + n;

            if (parseInt(condition) < songsList.length) this.number = parseInt(condition);

            else this.number = songsList.length;

        } else {
            this.number = n
        }
    }

    backspace() {
        let len = (this.number + "").length
        if (len && !this.ready_to_clear) {
            if (!this.reverse) {
                this.number = parseInt((this.number + "").substring(0, len - 1))
            } else {
                this.number = parseInt((this.number + "").substring(1, len))
            }
        } else if (this.ready_to_clear) {
            this.number = 0
        }
        this.ready_to_clear = false;
    }

    increment() {
        if (this.number >= songsList.length) {
            this.number = 1;
        }
        else {
            this.number++;
        }
    }
    decrement() {
        if (this.number <= 1) {
            this.number = songsList.length;
        }
        else {
            this.number--;
        }
    }
}


