export class LocalStorageService {
    public object: any;

    constructor() {
        let lss = LocalStorageService.getLocalStorage()
        if (lss) {
            this.object = lss
        } else {
            this.setLocalStorage(this.get_initial_value())
        }
    }

    get_initial_value(){
        return {
            last: 1,
            favorites: []
        }
    }

    static getLocalStorage() {
        let songBookFjff = localStorage.getItem('songBookFjff');
        if (songBookFjff) return JSON.parse(songBookFjff);
        return null;
    }
    setLocalStorage(data: any) {
        this.object = data;
        let a = JSON.stringify(data)
        localStorage.setItem('songBookFjff', a)
    }

    setLastStorage(num: number) {
        let new_object;
        if (this.object) {
            new_object = this.object
            new_object.last = num;
        } else {
            new_object = {
                last: num,
                favorites: []
            }
        }
        this.setLocalStorage(new_object)
    }

    setFavoritesStorage(new_favs: string[]) {
        let new_object = this.object
        new_object.favorites = new_favs;
        this.setLocalStorage(new_object)

    }
    
    add_one_favorite(num : number){
        let lss = LocalStorageService.getLocalStorage()
        if(!lss) lss = this.get_initial_value()
        lss.favorites.push(num)
        this.setLocalStorage(lss)
    }

    remove_one_favorite(num : number){
        let lss = LocalStorageService.getLocalStorage()
        let result = []
        for (let index = 0; index < lss.favorites.length; index++) {
            const element = lss.favorites[index];
            if (!(element == num)) result.push(element)
        }
        this.setFavoritesStorage(result)
    }
}


