import songsList from '../data/songs-list';

export class SongsService {
    public songs: any

    constructor() {
        this.songs = songsList
        // console.log(this.songs);
    }

    getSongByNumber(number: number) {
        for (let index = 0; index < this.songs.length; index++) {
            const element = this.songs[index];
            // console.log(element);
            if (element.number == number) {
                return element;
            }
        }
        return null;
    }

    getSongByTitle(title: string) {
        for (let index = 0; index < this.songs.length; index++) {
            const element = this.songs[index];
            // console.log(element);
            if (element.title == title) {
                return element;
            }
        }
        return null;
    }
}


