import { numberInputService } from './../../services/numberInput.service';
import { SongsService } from './../../services/songs.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import songsList from '../../data/songs-list';

@IonicPage()
@Component({
  selector: 'page-search',
  templateUrl: 'search.html',
})
export class SearchPage implements OnInit {

  @ViewChild('searchBar') myInput;
  public songs: any;
  // public searchInput: string;
  public pbt : any;
  public pbl : any;
  // public results_by_title = ["diams", "kely"];
  public customObject : any;
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public songsService: SongsService,
    public nbrInputService: numberInputService) {}

  ngOnInit() {
  }
  
  ionViewDidEnter(){
    this.songs = songsList;
    setTimeout(()=>{
      this.myInput.setFocus();
    })
    this.pbt = this.processByTitle("")
    this.pbl = this.processByLyric("")
    //  alert()
  }
  
  getItems(ev: any){
    let a = ev.target.value;
    console.log(a)
    this.pbt = this.processByTitle(a)
    this.pbl = this.processByLyric(a)
  }
  view_song(num: number) {
    this.nbrInputService.number = num;
    this.navCtrl.pop()
  }
  
  processByTitle(query: string) {
    console.log("called!")
    let x = new RegExp("^" + query, "i");
    let y = new RegExp(query, "i");
    let a = [];
    let b = [];
    for (let index = 0; index < this.songs.length; index++) {
      const element = this.songs[index];
      
      if (x.test(element.title)) {
        a.push(element.title)
      } else if (y.test(element.title)) {
        b.push(element.title)
      }
    }
    return [...a.sort(), ...b.sort()]
  }
  
  processByLyric(query: string) {
    console.log("processbylyric");
    
    let x = new RegExp(query, "i");
    let output = []
    if (!query || query.length < 4) {
      return null
    }
    for (let i = 0; i < this.songs.length; i++) {
      const element = this.songs[i];
      // console.log(element.number);
      
      let foo = [...element.verses,element.chorus]
      for (let j = 0; j < foo.length; j++) {
        let fragment = ""
        let num
        const text = foo[j];
        if (x.test(text)) {
          num = element.number
          // console.log(num);
          
          for (let k = 0; k < text.split("\n").length; k++) {
            const ligne = text.split("\n")[k];
            if (x.test(ligne)) {
              fragment = "..." + ligne + "..."
              break;
            }
          }
          output.push({
            number: num,
            fragment: fragment
          })
          break;
        }
        
      }
    }
    return output
  }
}


