import { LocalStorageService } from './../../services/localStorage.service';
import { SearchPage } from './../search/search';
import { numberInputService } from './../../services/numberInput.service';
import { ModalPage } from '../modal/modal';
import { FavorisPage } from '../favoris/favoris';
import { SongsService } from './../../services/songs.service';
import { Component, OnInit } from '@angular/core';
import { NavController, MenuController, ModalController, AlertController, Platform } from 'ionic-angular';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage implements OnInit {
  test: any;
  current_input: number;
  modal_showed: boolean;
  size: number;

  constructor(public navCtrl: NavController,
    public songs: SongsService,
    public menuCtrl: MenuController,
    public modalCtrl: ModalController,
    public nmbrInputService: numberInputService,
    public lss: LocalStorageService,
    public alertCtrl: AlertController,
    public platform: Platform) { }


  ngOnInit() {
    this.size = 20;
    this.test = this.songs.getSongByNumber(this.nmbrInputService.number);
    this.current_input
  }
  onOpenMenu() {
    this.menuCtrl.open();
  }

  goToSearch() {
    this.navCtrl.push(SearchPage)
  }

  loadFavoris() {
    this.navCtrl.push(FavorisPage)
    this.menuCtrl.close();
  }


  exit() {

    const confirm = this.alertCtrl.create({
      title: 'Fanamafisana',
      message: 'Hiala tanteraka?',
      buttons: [
        {
          text: 'Tsia',
          handler: () => {
            // console.log('Disagree clicked');
          }
        },
        {
          text: 'Eny',
          handler: () => {
            this.platform.exitApp()
          }
        }
      ]
    });
    confirm.present();
  }


addToFavorites(num : number){
  this.lss.add_one_favorite(num);
}
removeFromFavorites(num : number){
  this.lss.remove_one_favorite(num)
}

isFavorite(num : number){
  for (let index = 0; index < this.lss.object.favorites.length; index++) {
    const element = this.lss.object.favorites[index];
    if (num == element) {
      return true;
    }
  }
  return false;
}

showModal(){
  const modal = this.modalCtrl.create(ModalPage)
  modal.present()
  this.modal_showed = true;
  this.nmbrInputService.ready_to_clear = true;
  modal.onWillDismiss(() => {
    this.nmbrInputService.ready_to_clear = false;
    this.modal_showed = false;
  })
}

inc(){
  this.nmbrInputService.increment()
  this.lss.setLastStorage(this.nmbrInputService.number)
}

dec(){
  this.nmbrInputService.decrement()
  this.lss.setLastStorage(this.nmbrInputService.number)
}
show_number(){
  if (!this.nmbrInputService.number) {
    return "    "
  }
  let foo = this.nmbrInputService.number + ""
  switch (foo.length) {
    case 1:
      return "  " + foo + "  "
    case 2:
      return " " + foo + " "
    case 3:
      return foo
  }
}
showConfirm(num : number) {
  const title = this.songs.getSongByNumber(num).title
  let message
  let isFavorite = this.isFavorite(num)
  if (isFavorite) message = 'Irinao hiala ao amin\' ≪ Ireo Hirako ≫ ny hira faha ' + num + " : " + title + '?'
  else message = 'Irinao hiditra ao amin\' ≪ Ireo Hirako ≫ ny hira faha ' + num + " : " + title + '?'
  const confirm = this.alertCtrl.create({
    title: 'Ireo Hirako',
    message: message,
    buttons: [
      {
        text: 'Tsia',
        handler: () => {
          if (isFavorite) this.addToFavorites(num)
          else this.removeFromFavorites(num)
          // console.log('Disagree clicked');
        }
      },
      {
        text: 'Eny',
        handler: () => {
          if (isFavorite) this.removeFromFavorites(num)
          else this.addToFavorites(num)
          // console.log('Agree clicked');
        }
      }
    ]
  });
  confirm.present();
}
}


