import { SongsService } from './../../services/songs.service';
import { LocalStorageService } from './../../services/localStorage.service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, ActionSheetController } from 'ionic-angular';
import { numberInputService } from '../../services/numberInput.service';

@IonicPage()
@Component({
  selector: 'page-favoris',
  templateUrl: 'favoris.html',
})
export class FavorisPage {

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public lss : LocalStorageService,
              public songService : SongsService,
              public songs : numberInputService,
              public alertCtrl : AlertController,
              public actionSheetCtrl : ActionSheetController) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad FavorisPage');
  }
  gotoSong(num : number){
    this.songs.number = num;
    this.navCtrl.pop()
  }
  onRemoveFavorite(num : number, title: string){
    const confirm = this.alertCtrl.create({
      title: 'Ireo Hirako',
      message: 'Irina hiala ato amin\'≪ Ireo Hirako ≫ ny hira '+num+'.'+title+'?',
      buttons: [
        {
          text: 'Tsia',
          handler: () => {
            // console.log('Disagree clicked');
          }
        },
        {
          text: 'Eny',
          handler: () => {
            this.lss.remove_one_favorite(num)
            // console.log('Agree clicked');
          }
        }
      ]
    });
    confirm.present();
  }

  presentActionSheet() {
    const confirm = this.alertCtrl.create({
      title: 'Ireo Hirako',
      message: 'Hamafa ≪ Ireo Hirako ≫ rehetra ?',
      buttons: [
        {
          text: 'Tsia',
          handler: () => {
            // console.log('Disagree clicked');
          }
        },
        {
          text: 'Eny',
          handler: () => {
            this.lss.setFavoritesStorage([])
            // console.log('Agree clicked');
          }
        }
      ]
    });
    confirm.present();
  }

}



